.Project{
    text-align: center;
    justify-content: center;
    background-color: skyblue;
}
.Project h1{
    margin-top: 4rem;
    font: large;
}
.Project p{
    text-align: center;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    font-size: 15px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.Project button{
    padding: 1rem 4rem;
    margin-top: 1rem;
    border-radius: 1rem;
}
.btn2{
    border: none;
    background: transparent;
    padding: 6px 16px;
    font-size: 1.5rem;
    color: white;
    background: black;
    border-radius: 5px;
}
.btn2:hover{
    color: red;
    background: black;
}